@import 'variables.less';
@font-face {
  font-family: 'ArcadeRounded';
  src: url('fonts/arcade_r.ttf') format('truetype');
  font-weight: normal;
  font-style: normal; 
}

body{
	height: 100%;
	background-color: @assistant0;
	background-size: cover;
}
#logo img{
	display: block;
	padding: 20px 0;
}
#game-view{
	display: block;
	canvas{
		margin: 80px auto;
		display: block;
		margin-bottom: 30px;
		width: 100%;
		max-width: 480px;
	}
}
#menu-view {
	margin: 0 auto;
	display: block;
	.btn-group-vertical{
		margin: 0 auto;
		display: block;
		.btn.btn-default{
			font-family: 'ArcadeRounded',Helvetica,Arial,sans-serif;
			line-height: 30px;
			vertical-align: middle;
			text-align: center;
			background-color: @assistant0;
			color: @assistant3;
			border:2px solid @assistant3;
			border-radius: 4px;
			max-width: 300px;
			margin: 0 auto 20px auto;
			box-shadow: none;
			outline-style: none;
			&:first-of-type{
				margin-top:20px;
			}
			&:last-of-type{
				margin-top:60px;
			}
			&:hover{
				border-color: @assistant8;
				color: @assistant8;
			}
		}
	}
}
#login-view{
	font-family: 'ArcadeRounded',Helvetica,Arial,sans-serif;
	margin: 0 auto;
	display: block;
	label{
		width: 100%;
		text-align: center;
		line-height: 30px;
		vertical-align: middle;
		color: @assistant8;
		margin-top:30px;
	}
	.btn-group-vertical{
		margin: 0 auto;
		display: block;
		.btn{
			color: @assistant8;
			line-height: 30px;
			margin: 20px auto 0 auto;
			max-width: 300px;
			border-width: 2px;
			border-radius: 4px;
			font-size: 13px;
			border-color: @game-color-2;
			box-shadow: none;
			outline-style: none;
			&:hover{
				border-color: @game-color-1;
			}
			&.btn-primary{
				background-color: @fb-color;
			}
			&.btn-secondary{
				background-color: @twitter-color;
			}
		}
	}
}
#profile-view{
	font-family: 'ArcadeRounded',Helvetica,Arial,sans-serif;
	margin: 0 auto;
	display: block;
	h3{
		color:@game-color-3;
		text-align: center;
		margin-top: 0;
	}
	ul{
		list-style: none;
		padding-left: 0;
		.input-group{
			margin-top: 20px;
			vertical-align: middle;
			input{
				font-family: 'ArcadeRounded',Helvetica,Arial,sans-serif;
				height: 46px;
				border:2px solid @assistant8;
				border-left: 0;
				&.form-control{
					background-color: @assistant0;
					color:@assistant8;
					border-radius: 4px;
					border-bottom-left-radius: 0;
					border-top-left-radius: 0;
					text-align: right;
					line-height: 46px;
					height: 46px;
					&:focus{
						outline-style: none;
						box-shadow: none;
						transition:none;
					}
				}
			}
			.btn{
				background-color: @assistant0;
				color:@assistant8;
				border:2px solid @assistant8;
				border-radius: 4px;
				max-width: 300px;
				font-size: 13px;
				line-height: 30px;
				box-shadow: none;
				outline-style: none;
				&.btn-secondary.icon{
					margin-left: 10px;
					border-color: @assistant3;
					color:@assistant3;
					&:hover,&.selected{
						border-color: @assistant8;
						color:@assistant8;
					}
					span{
						font-size: 18px;
						vertical-align: middle;
					}
				}
				&.btn-secondary.disabled{
					border-right: 0;
					opacity: 1;
					cursor: default;
					border-top-right-radius: 0;
					border-bottom-right-radius: 0;
					outline-style: none;
				}
			}
		}
		.btn-group{
			margin: 20px auto 0 auto;
			width: 100%;
			.btn{
				width: 45%;
				border-radius: 4px;
				background-color: @assistant0;
				border:2px solid @assistant3;
				color:@assistant3;
				height: 44px;
				box-shadow: none;
				outline-style: none;
				overflow: hidden;
				&.btn-primary{
					margin-right: 5%;
				}
				&.btn-secondary{
					margin-left: 5%;
				}
				&:hover{
					border-color: @assistant8;
					color:@assistant8;
				}
				&.disabled{
					border-color: @assistant2;
					color: @assistant2;
				}
			}
		}
	}

}
footer .alert{
	display: block;
	position: absolute;
	bottom: 0;
	margin-bottom: 0;
	width: 100%;
	font-family: 'ArcadeRounded',Helvetica,Arial,sans-serif;
	text-align: center;
}